.submit {
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: transparent;
  padding: 6px 12px;
  color: rgba(255, 255, 255, 0.2);
  min-width: 80px;
  -webkit-transition: all ease .4s;
  transition: all ease .4s;
}
.submit.active {
  border-color: #E3E3E3;
  color: #E3E3E3;
  cursor: pointer;
  position: relative;
}
.submit.active:before {
  content: '';
  height: 100%;
  width: 0%;
  -webkit-transition: all ease .4s;
  transition: all ease .4s;
  display: block;
  top: 0;
  left: 0;
  position: absolute;
  z-index: -1;
}
.submit.active:hover:before {
  width: 100%;
  background-color: #ef1925;
}
